
import React, { Component } from 'react';
import { NavLink } from'react-router-dom';

class Registerpage extends Component {
    constructor(props) {
        super(props)
        this.state = {   
            register :{  
            name: '',
            password: '',   
            DOB: '',
            gender: '',
            hobbies: [],
            address: '',
            city: '',
            },
            error : {
            name: false,
            password:false, 
            DOB: false,
            gender: false,
            hobbies: false,
            address: false,
            city: false,
            }
        }  
    }  

    onHandleInput = (event) => {   
        if (event.target.type === "checkbox") {
            if (event.target.checked) { 
                this.state.register.hobbies.push(event.target.value);    
            }   
            else {
                const index = this.state.register.hobbies.findIndex((value) => {    
                    return value === event.target.value   
                });
                this.state.register.hobbies.splice(index, 1);
            }
            this.setState({
                register : {...this.state.register , hobbies: this.state.register.hobbies }
            })
        } 
        else { 
            this.setState({
                register : {...this.state.register , [event.target.name] : event.target.value  }
            })
        } 
    }
    createaccount() {
        console.log(this.state.register)

        for ( let key in this.state.register){
            if(key === 'hobbies'){
                if(this.state.register.hobbies.length === 0){
                    this.state.error.hobbies = true;
                } 
                else{
                    this.state.error.hobbies = false;
                }
            }
            else if(this.state.register[key] === '') {
                this.state.error[key] = true;
            }
            else{
                this.state.error[key] = false;
            }
        }
        this.setState({
            error : this.state.error
        })
    }

    onInputFocus = (event) => {
        this.setState({
            error : {...this.state.error, [event.target.name] : false}
        })
    }
    onInputBlur = (event) => {
        const key = "error_"+(event.target.name);
        const value = (event.target.value);
        if(value.length ===0 ) {
            this.setState({
                error : { ...this.state.error , [event.target.name] : true}
            })
        }
    }

    render() {
    
        return (
            <div className="registerform">

                <h2>Create your Account</h2>
                <div className="m-top-15">
                    <label className="register_label">Enter your Name</label>
                    <input className="inputbox" type="text" name="name" placeholder="Enter your name" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}/>
                    { this.state.error.name &&
                        <div>
                            <span className="err_msg">Please Enter a vaild user name </span>
                        </div>
                    }
                </div>
                <div className="m-top-15">
                    <label className="register_label">Enter your email</label>
                    <input className="inputbox" type="email" name="email" placeholder="Enter your email" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}/>
                    { this.state.error.email &&
                        <div>
                            <span className="err_msg">Please Enter a vaild email id </span>
                        </div>
                    }
                </div>
                <div className="m-top-15">
                    <label className="register_label">Enter your password</label>
                    <input className="inputbox" type="password" name="password" placeholder="Enter your password" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}/>
                    { this.state.error.password &&
                        <div>
                            <span className="err_msg">Please Enter a vaild password </span>
                        </div>
                    }
                </div>
                <div className="m-top-15">
                    <label className="register_label">Enter your DOB</label>
                    <input className="inputbox" name="DOB" type="date" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}/>
                    { this.state.error.DOB &&
                        <div>
                            <span className="err_msg">Please Enter your date of brith </span>
                        </div>
                    }
                </div>
                <div className="m-top-15">
                    <label className="register_label">select your gender</label>
                    <input type="radio" name="gender" value="male" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}/>male
                    <input type="radio" name="gender" value="female" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}/>female
                    <input type="radio" name="gender" value="others" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur}/>others
                    { this.state.error.gender &&
                        <div>
                            <span className="err_msg">Please select your gender </span>
                        </div>
                    }
                </div>
                <div className="m-top-15 hobbies" >
                    <label className="register_label ">Select your Hobbies</label>
                    <div className="checkboxes">
                        <label><input name="hobbies" type="checkbox" onChange={this.onHandleInput} onBlur={this.onInputBlur} onFocus={this.onInputFocus} value="Cricket" />Cricket</label>
                        <label><input name="hobbies" type="checkbox" onChange={this.onHandleInput} onBlur={this.onInputBlur} onFocus={this.onInputFocus} value="Football" />Football</label>
                        <label><input name="hobbies" type="checkbox" onChange={this.onHandleInput} onBlur={this.onInputBlur} onFocus={this.onInputFocus} value="Hockey" />Hockey</label>
                        <label><input name="hobbies" type="checkbox" onChange={this.onHandleInput} onBlur={this.onInputBlur} onFocus={this.onInputFocus} value="Basketball" />Basketball</label>
                        <label><input name="hobbies" type="checkbox" onChange={this.onHandleInput} onBlur={this.onInputBlur} onFocus={this.onInputFocus} value="Tennis" />Tennis</label>
                        { this.state.error.hobbies &&
                        <div>
                            <span className="err_msg">Please select your hobbies </span>
                        </div>
                    }
                    </div>
                </div>
                <div className="m-top-15">
                    <label className="register_label">Enter your Address</label>
                    <textarea className="inputarea" name="address" placeholder="Enter your Address" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur} ></textarea>
                    { this.state.error.address &&
                        <div>
                            <span className="err_msg">Please Enter a vaild address </span>
                        </div>
                    }
                </div>
                <div className="m-top-15">
                    <label className="register_label">select your city</label>
                    <select className="inputbox" defaultValue="" onChange={this.onHandleInput} onFocus={this.onInputFocus} onBlur={this.onInputBlur} name="city">
                        <option value="" disabled >please Select your city</option>
                        <option value="chennai">chennai</option>
                        <option value="Madurai">Madurai</option>
                        <option value="selam">selam</option>
                        <option value="sivakasi">sivakasi</option>
                        <option value="trichy">trichy</option>
                        <option value="Coimbatore">Coimbatore</option>
                    </select>
                    { this.state.error.city &&
                        <div>
                            <span className="err_msg">Please select your city </span>
                        </div>
                    }
                </div>
                <div className="m-top-15">
                    <button className="inputbox" onClick={() => { this.createaccount() }}>create My Accout</button>
                </div>
                <NavLink  to ="/login">go to login page</NavLink>
                
            </div>
        )
    }

}

export default Registerpage;