import React, { Component } from 'react';

import DataSharingContext from '../context.js/Data-Sharing-Context';


class Search extends Component {

    onHandleChange =(event) => {
        this.context.fromSearch(event.target.value);
    }
    render() {
        return (
            <DataSharingContext.Consumer>
                {
                    (context) => {
                        return (
                            <div>
                                <input className="searchbox" type="text" placeholder=" Enter a value" onChange={this.onHandleChange}/>
                            </div>
                        )
                    }
                }
                
            </DataSharingContext.Consumer>
        );
    }
}

Search.contextType=DataSharingContext;
export default Search;