import axios from 'axios';
import React, { Component } from 'react';

class SentPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            createUserList: {
                name: '',
                job: ''
            }
        }
    }

    onHandleInput = (event) => {
        this.setState({
            createUserList: { ...this.state.createUserList, [event.target.name]: event.target.value }
        })
    }

    onCreateUser() {
        /* console.log(this.state.createUserList);
        const url = "https://reqres.in/api/users";
        axios.post(url, this.state.createUserList)
            .then((response) => {
                console.log(response)
            })
            .catch((error) => {
                console.log(error)
            }) */
        console.log(this.state.createUserList);
        const url = "https://reqres.in/api/users/2";
        axios.put(url, this.state.createUserList)
            .then((response) => {
                console.log(response)
            })
            .catch((error) => {
                console.log(error)
            }) 
    }
    render() {
        return (
            <div>
                <h1>This is a sent Page</h1>
                <div>
                    <label>Enter User Name :</label>
                    <input type="text" name="name" placeholder="Please enter user name.." onChange={this.onHandleInput} />
                </div>
                <div>
                    <label>Enter Job :</label>
                    <input type="text" name="job" placeholder="Please enter job.." onChange={this.onHandleInput} />
                </div>
                <div>
                    <button onClick={() => this.onCreateUser()}>Create User</button>
                </div>
            </div>
        )
    }
}


export default SentPage;