import React, { Component } from 'react';

import DataSharingContext from '../context.js/Data-Sharing-Context';


class ProductList extends Component {
    render() {
        return (
            <DataSharingContext.Consumer>
                {
                    (context) => {

                        return (
                            <div>
                                <h1>This is a Product list</h1>
                                <h1>This result is {context.searchMessage}</h1>
                            </div>
                        )
                    }
                }
              
            </DataSharingContext.Consumer>
        );
    }
}

ProductList.contextType=DataSharingContext;
export default ProductList;